
import { defineComponent, ref, toRefs, watchEffect } from "vue";

import OverallCard from "@/components/OverallCard.vue";
import LineBarChartCard from "@/components/LineBarChartCard.vue";
import BarChartCard from "@/components/BarChartCard.vue";
import {
  LineBarChartResponse,
  StackBarChartResponse
} from "@/typings/brandInsight";
import {
  getGoogleTrendChart,
  getInstagramGrowthRate,
  getOverallChart,
  getTopRecycleMaterialsUsedChart
} from "@/apis/brandInsight";
import { formatLineBarData, formatStackBarData } from "@/utils/brandInsight";

const Component = defineComponent({
  name: "BrandPower",
  props: {
    brand: {
      type: String,
      default: ""
    }
  },
  components: {
    OverallCard,
    LineBarChartCard,
    BarChartCard
  },
  setup(props) {
    const { brand } = toRefs(props);
    const outlookNumber = ref<number | undefined>(undefined);
    const managementNumber = ref<number | undefined>(undefined);
    const growthNumber = ref<number | undefined>(undefined);
    const googleTrendChart = ref<LineBarChartResponse | null>(null);
    const topRecycleMaterialsUsed = ref<StackBarChartResponse | null>(null);

    watchEffect(async () => {
      if (!brand.value) return;

      const { data: outlook } = await getOverallChart(
        "Positive Business Outlook",
        brand.value
      );
      const { data: management } = await getOverallChart(
        "Senior Management",
        brand.value
      );
      const { data: growth } = await getInstagramGrowthRate(brand.value);
      const { data: googleTrend } = await getGoogleTrendChart(brand.value);
      const { data: topRecycle } = await getTopRecycleMaterialsUsedChart(
        brand.value
      );
      outlookNumber.value = outlook.Value;
      managementNumber.value = management.Value;
      growthNumber.value = growth.growth_rate;
      googleTrendChart.value = googleTrend;
      topRecycleMaterialsUsed.value = topRecycle;
    });

    return {
      outlookNumber,
      managementNumber,
      growthNumber,
      googleTrendChart,
      topRecycleMaterialsUsed,

      formatLineBarData,
      formatStackBarData
    };
  }
});

export default Component;
