
import { defineComponent, onMounted, ref } from "vue";
import { useRoute, useRouter } from "vue-router";

import { Pane } from "element-plus/lib/el-tabs/src/tabs.vue";
import { getBrandOptions } from "@/apis/brandInsight";
import { OptionItem } from "@/typings/asideFilter";

const Component = defineComponent({
  name: "BrandInsights",
  setup() {
    const router = useRouter();
    const route = useRoute();

    const searchValue = ref("adidas");
    const tabValue = ref("");
    const searchOptions = ref<OptionItem[]>([]);

    const onTabClick = (value: Pane) => {
      router.push({ name: value.props.name });
    };

    const init = async () => {
      const { data } = await getBrandOptions();
      searchOptions.value = data.brands.map((brand: string): OptionItem => ({label: brand, value: brand, checked: false}));
    };

    onMounted(() => {
      if (route.name) {
        tabValue.value = route.name as string;
      }
      init();
    });

    return {
      searchValue,
      tabValue,
      searchOptions,

      onTabClick
    };
  }
});

export default Component;
