import dayjs from "dayjs";

import {
  FinanceRegionChartResponse, LineBarChartResponse, LineChartResponse,
  PieChartResponse,
  PieItemResponse, StackBarChartResponse,
  TableResponse
} from "@/typings/brandInsight";
import { PieDataItemOption } from "echarts/types/src/chart/pie/PieSeries";

export function formatLineBarData(data: LineBarChartResponse | null) {
  if (!data) return;

  const _data = data.data;
  const axis = _data.line_trend.map((it: [string, number]) =>
    dayjs(it[0]).format("YYYY-MM-DD")
  );
  const lineData = _data.line_trend.map((it: [string, number]) => it[1]);
  const barData = _data.bar_changes.map((it: [string, number]) => it[1]);
  return {
    axis,
    lineData,
    barData
  };
}

export function formatLineData(data: LineChartResponse | null) {
  if (!data) return;

  const _data = data.data;
  const axis = _data.line_trend.map((it: [string, number]) =>
    dayjs(it[0]).format("YYYY-MM-DD")
  );
  const lineData = _data.line_trend.map((it: [string, number]) => it[1]);
  return {
    axis,
    lineData,
  };
}

export function formatStackBarData(data: StackBarChartResponse | null) {
  if (!data) return;

  const _data = data.data;
  const axis = _data.x.map((it: string) => dayjs(it).format("YYYY-MM-DD"));
  const series = _data.c.map((name: string, index: number) => ({
    type: "bar",
    name,
    stack: "one",
    data: _data.y[index]
  }));
  return {
    axis,
    series
  };
}

export function formatTableData(data: TableResponse | null) {
  if (!data) return;

  return data.data.map((it: [string, number, number]) => ({
    category: it[0],
    products: it[1],
    percentage: it[2]
  }));
}

export function formatPieChartData(data: PieChartResponse | null) {
  if (!data) return;

  return data.data.map(
    (it: PieItemResponse): PieDataItemOption => ({
      name: it.label,
      value: it.data
    })
  );
}

export function formatFinanceRegionData(data: FinanceRegionChartResponse | null) {
  if (!data) return;

  const axis = data.datetimes.map((datetime: string) => dayjs(datetime).format('YYYY/MM'));
  const lineData = data.values.map((value: string) => parseFloat(value));
  return {
    axis,
    lineData,
  };
}
