<template>
  <div id="line-chart-card-component">
    <div class="top-container">
      <h3 class="title">{{ title }}</h3>
      <div v-if="hasData" class="expand-button" @click="onExpandClick">
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>
    <div v-if="hasData" class="chart-container">
      <div :id="'line-chart-main-' + title" class="chart-main"></div>
    </div>
    <div v-else class="chart-empty">
      <h3>No Data</h3>
    </div>

    <el-dialog :title="title" v-model="dialogVisible" width="80%">
      <div class="dialog-container">
        <div :id="'line-chart-dialog-' + title" class="chart-dialog"></div>
        <p class="hint">Scrolling to zoom.</p>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  ref,
  toRefs,
  watchEffect,
  computed,
  nextTick,
  PropType
} from "vue";
import * as echarts from "echarts/core";
import {
  LineChart,
  LineSeriesOption,
  BarChart,
  BarSeriesOption
} from "echarts/charts";
import {
  GridComponent,
  GridComponentOption,
  TooltipComponent,
  TooltipComponentOption,
  DataZoomComponent,
  DataZoomComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";
import { AddResizer } from "@/typings/store";
import { useStore } from "vuex";

type ECOption = echarts.ComposeOption<
  | LineSeriesOption
  | BarSeriesOption
  | GridComponentOption
  | TooltipComponentOption
  | DataZoomComponentOption
>;

echarts.use([
  LineChart,
  BarChart,
  GridComponent,
  TooltipComponent,
  DataZoomComponent,
  CanvasRenderer
]);

function takeTypeText(type: string) {
  const adapter: Adapter<string> = {
    thousand: "K",
    percentage: "%",
    default: ""
  };
  return adapter[type] || adapter.default;
}

const Component = defineComponent({
  name: "LineBarChart",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Object as PropType<{
        axis: string[];
        lineData: number[];
        barData: number[];
      }>,
      default: () => ({
        axis: [],
        lineData: [],
        barData: []
      })
    },
    names: {
      type: Array as PropType<string[]>,
      default: () => ["A", "B"]
    },
    type: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const store = useStore();
    const { title, data, names, type } = toRefs(props);
    const dialogVisible = ref(false);
    const hasData = computed(() => {
      return data.value.lineData.length || data.value.barData.length
    })

    let option: ECOption | null = null;

    watchEffect(async () => {
      option = {
        tooltip: {
          trigger: "axis"
        },
        xAxis: {
          type: "category",
          data: data.value.axis
        },
        yAxis: [
          {
            type: "value",
            axisLabel: {
              formatter(value: any) {
                return value === 0 ? value : value + takeTypeText(type.value)
              }
            }
          },
          {
            type: "value",
            axisLabel: {
              formatter(value: any) {
                return value === 0 ? value : value + takeTypeText(type.value)
              }
            }
          }
        ],
        series: [
          {
            type: "line",
            name: names.value[0],
            data:
              type.value === "thousand"
                ? data.value.lineData.map((it: number) => Math.round(it / 1000))
                : data.value.lineData,
            yAxisIndex: 0
          },
          {
            type: "bar",
            name: names.value[1],
            data:
              type.value === "thousand"
                ? data.value.barData.map((it: number) => Math.round(it / 1000))
                : data.value.barData,
            yAxisIndex: 1
          }
        ]
      };

      if (type.value) {
        (option!.tooltip as TooltipComponentOption).formatter = function(
          params: any
        ) {
          return `
            <div style="min-width: 240px;">
              ${params[0].name} <br />
                ${params
                  .map(
                    (it: any) => `
                  <div  style="display: flex; justify-content: space-between;">
                    <span>${it.marker} ${it.seriesName}</span> ${
                      it.value
                    }${takeTypeText(type.value)} <br />
                  </div>
                `
                  )
                  .join("")}
            </div>
          `;
        };
      }

      await nextTick();
      const chart = echarts.init(
        document.getElementById("line-chart-main-" + title.value)!
      );
      chart.setOption(option);
      store.commit(AddResizer, chart);
    });

    const onExpandClick = async () => {
      dialogVisible.value = true;

      await nextTick();
      const chart = echarts.init(
        document.getElementById("line-chart-dialog-" + title.value)!
      );
      chart.setOption({
        ...option,
        dataZoom: [
          {
            type: "inside"
          }
        ]
      });
      store.commit(AddResizer, chart);
    };

    return {
      dialogVisible,
      hasData,

      onExpandClick
    };
  }
});

export default Component;
</script>

