#line-chart-card-component
  width 100%
  background-color white

  .top-container
    display flex
    justify-content space-between
    align-items center
    padding 20px 30px 0
    .title
      font-size $font-size-lg2x
      font-weight 500
    .expand-button
      cursor pointer
      img
        width 20px
        height 20px

  .chart-container
    width 100%
  .dialog-container
    width 100%
    position relative
  .chart-main
    chart-main()
  .chart-empty
    chart-empty()
  .chart-dialog
    chart-dialog()

  .hint
    position absolute
    right 30px
    bottom 5px
</style>
