<template>
  <div id="pie-chart-card-component">
    <div class="top-container">
      <h3 class="title">
        {{ title }}
        <p class="subtitle">last 30 Days</p>
      </h3>
      <div v-if="hasData" class="expand-button" @click="onExpandClick">
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>
    <div v-if="hasData" class="chart-container">
      <div :id="'pie-chart-main-' + title" class="chart-main"></div>
    </div>
    <div v-else class="chart-empty">
      <h3>No Data</h3>
    </div>

    <el-dialog :title="title" v-model="dialogVisible" width="80%">
      <div class="dialog-container">
        <div :id="'pie-chart-dialog-' + title" class="chart-dialog"></div>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  ref,
  toRefs,
  watchEffect,
  nextTick,
  computed,
  PropType
} from "vue";
import * as echarts from "echarts/core";
import { PieChart, PieSeriesOption } from "echarts/charts";
import {
  GridComponent,
  GridComponentOption,
  LegendComponent,
  LegendComponentOption,
  TooltipComponent,
  TooltipComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";
import { PieDataItemOption } from "echarts/types/src/chart/pie/PieSeries";
import { AddResizer } from "@/typings/store";
import { useStore } from "vuex";

type ECOption = echarts.ComposeOption<
  | PieSeriesOption
  | GridComponentOption
  | LegendComponentOption
  | TooltipComponentOption
>;

echarts.use([
  PieChart,
  GridComponent,
  LegendComponent,
  TooltipComponent,
  CanvasRenderer
]);

const Component = defineComponent({
  name: "PieChart",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Array as PropType<PieDataItemOption[]>,
      default: () => []
    }
  },
  setup(props) {
    const store = useStore();
    const { title, data } = toRefs(props);
    const dialogVisible = ref(false);
    const hasData = computed(() => {
      return data.value.length;
    });

    let option: ECOption | null = null;

    watchEffect(async () => {
      if (!hasData.value) return;

      const total = data.value.reduce(
        (sum, item: PieDataItemOption) => sum + (item.value as number),
        0
      );

      option = {
        legend: {
          type: "scroll",
          bottom: 0
        },
        tooltip: {
          trigger: "item",
          formatter(param: any) {
            return `
              <div style="display: flex; justify-content: space-between;">
                <span style="margin-right: 15px;">${param.marker} ${
              param.name
            }</span> ${Math.round((param.value * 100) / total)}% <br />
              </div>
            `;
          }
        },
        series: [
          {
            type: "pie",
            radius: "50%",
            data: data.value,
            label: {
              show: false
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };

      await nextTick();
      const chart = echarts.init(
        document.getElementById("pie-chart-main-" + title.value)!
      );
      chart.setOption(option);
      store.commit(AddResizer, chart);
    });

    const onExpandClick = async () => {
      dialogVisible.value = true;

      await nextTick();
      const chart = echarts.init(
        document.getElementById("pie-chart-dialog-" + title.value)!
      );
      chart.setOption({
        ...option,
        series: [
          {
            // @ts-ignore
            ...option.series[0],
            label: {
              show: true
            }
          }
        ]
      });
      store.commit(AddResizer, chart);
    };

    return {
      dialogVisible,
      hasData,

      onExpandClick
    };
  }
});

export default Component;
</script>

