
import {
  defineComponent,
  ref,
  toRefs,
  watchEffect,
  nextTick,
  computed,
  PropType
} from "vue";
import * as echarts from "echarts/core";
import { RadarChart, RadarSeriesOption } from "echarts/charts";
import {
  GridComponent,
  GridComponentOption,
  TooltipComponent,
  TooltipComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";
import { AddResizer } from "@/typings/store";
import { useStore } from "vuex";
import { RadarChartResponse } from "@/typings/brandInsight";

type ECOption = echarts.ComposeOption<
  RadarSeriesOption | GridComponentOption | TooltipComponentOption
>;

echarts.use([RadarChart, GridComponent, TooltipComponent, CanvasRenderer]);

interface Indicator {
  name: string;
  min: number;
  max: number;
}

function calcPercentage(value: number | string, indicator: Indicator) {
  if (typeof value === "string") return "NA";
  if (
    ["net_profit_margin", "debt_asset_ratio", "return_on_assets"].includes(
      indicator.name
    )
  ) {
    return Math.round(value * 100) + "%";
  }
  return value;
}

const Component = defineComponent({
  name: "RadarChartCard",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Object as PropType<RadarChartResponse>,
      default: () => ({
        labels: [],
        maxs: [],
        mins: [],
        values: []
      })
    }
  },
  setup(props) {
    const store = useStore();
    const { title, data } = toRefs(props);
    const dialogVisible = ref(false);
    const hasData = computed(() => {
      return data.value.values.length;
    });

    let option: ECOption | null = null;

    watchEffect(async () => {
      if (!hasData.value) return;

      const indicator = data.value.labels.map(
        (label: string, index: number): Indicator => ({
          name: label,
          min: data.value.mins[index],
          max: data.value.maxs[index]
        })
      );

      option = {
        tooltip: {
          trigger: "item",
          formatter(param: any) {
            return `
            <div style="min-width: 200px;">
              ${indicator
                .map(
                  (indicator: Indicator, index: number) =>
                    `
                  <div  style="display: flex; justify-content: space-between;">
                    <span>${param.marker} ${
                      indicator.name
                    }</span> ${calcPercentage(
                      param.value[index],
                      indicator
                    )} <br />
                  </div>
                `
                )
                .join("")}
            </div>
          `;
          }
        },
        radar: {
          name: {
            textStyle: {
              color: "#fff",
              backgroundColor: "#999",
              borderRadius: 3,
              padding: [3, 5]
            }
          },
          scale: true,
          indicator
        },
        series: [
          {
            type: "radar",
            data: [
              {
                value: data.value.values
              }
            ]
          }
        ]
      };

      await nextTick();
      const chart = echarts.init(
        document.getElementById("radar-chart-main-" + title.value)!
      );
      chart.setOption(option);
      store.commit(AddResizer, chart);
    });

    const onExpandClick = async () => {
      if (!option) return;
      dialogVisible.value = true;

      await nextTick();
      const chart = echarts.init(
        document.getElementById("radar-chart-dialog-" + title.value)!
      );
      chart.setOption(option);
      store.commit(AddResizer, chart);
    };

    return {
      dialogVisible,
      hasData,

      onExpandClick
    };
  }
});

export default Component;
