<template>
  <div id="brand-power-component">
    <h2 class="zoom-title pt-0">Overall</h2>
    <el-row :gutter="30">
      <el-col :span="8">
        <OverallCard
          type="pie"
          title="Positive Business Outlook"
          :value="outlookNumber && Math.round(outlookNumber * 100)"
          :max="100"
        ></OverallCard>
      </el-col>
      <el-col :span="8">
        <OverallCard
          type="growth"
          title="IG Follower WOW Growth"
          :value="growthNumber"
        ></OverallCard>
      </el-col>
      <el-col :span="8">
        <OverallCard
          type="pie"
          title="Senior Management Rating"
          :value="managementNumber && Math.round(managementNumber)"
          :max="5"
        ></OverallCard>
      </el-col>
    </el-row>

    <el-row :gutter="30">
      <el-col :span="12">
        <h2 class="zoom-title">Search Volume</h2>
        <LineBarChartCard
          title="Google Search Volume"
          type="thousand"
          :data="formatLineBarData(googleTrendChart)"
          :names="['Volume', 'Compared to last month']"
        ></LineBarChartCard>
      </el-col>
      <el-col :span="12">
        <h2 class="zoom-title">Sustainability</h2>
        <BarChartCard
          title="Top Recycled Materials Used of New Arrivals"
          :data="formatStackBarData(topRecycleMaterialsUsed)"
        ></BarChartCard>
      </el-col>
    </el-row>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRefs, watchEffect } from "vue";

import OverallCard from "@/components/OverallCard.vue";
import LineBarChartCard from "@/components/LineBarChartCard.vue";
import BarChartCard from "@/components/BarChartCard.vue";
import {
  LineBarChartResponse,
  StackBarChartResponse
} from "@/typings/brandInsight";
import {
  getGoogleTrendChart,
  getInstagramGrowthRate,
  getOverallChart,
  getTopRecycleMaterialsUsedChart
} from "@/apis/brandInsight";
import { formatLineBarData, formatStackBarData } from "@/utils/brandInsight";

const Component = defineComponent({
  name: "BrandPower",
  props: {
    brand: {
      type: String,
      default: ""
    }
  },
  components: {
    OverallCard,
    LineBarChartCard,
    BarChartCard
  },
  setup(props) {
    const { brand } = toRefs(props);
    const outlookNumber = ref<number | undefined>(undefined);
    const managementNumber = ref<number | undefined>(undefined);
    const growthNumber = ref<number | undefined>(undefined);
    const googleTrendChart = ref<LineBarChartResponse | null>(null);
    const topRecycleMaterialsUsed = ref<StackBarChartResponse | null>(null);

    watchEffect(async () => {
      if (!brand.value) return;

      const { data: outlook } = await getOverallChart(
        "Positive Business Outlook",
        brand.value
      );
      const { data: management } = await getOverallChart(
        "Senior Management",
        brand.value
      );
      const { data: growth } = await getInstagramGrowthRate(brand.value);
      const { data: googleTrend } = await getGoogleTrendChart(brand.value);
      const { data: topRecycle } = await getTopRecycleMaterialsUsedChart(
        brand.value
      );
      outlookNumber.value = outlook.Value;
      managementNumber.value = management.Value;
      growthNumber.value = growth.growth_rate;
      googleTrendChart.value = googleTrend;
      topRecycleMaterialsUsed.value = topRecycle;
    });

    return {
      outlookNumber,
      managementNumber,
      growthNumber,
      googleTrendChart,
      topRecycleMaterialsUsed,

      formatLineBarData,
      formatStackBarData
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#brand-power-component
  overflow-x hidden
  padding-top 15px

  .zoom-title
    padding 30px 0 20px
    zoom-title()

  .pt-0
    padding-top 0

  :deep() .el-row
    el-row()
</style>
