
import {
  defineComponent,
  ref,
  toRefs,
  watchEffect,
  nextTick,
  computed,
  PropType
} from "vue";
import * as echarts from "echarts/core";
import { LineChart, LineSeriesOption } from "echarts/charts";
import {
  GridComponent,
  GridComponentOption,
  TooltipComponent,
  TooltipComponentOption,
  DataZoomComponent,
  DataZoomComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";
import { AddResizer } from "@/typings/store";
import { useStore } from "vuex";

type ECOption = echarts.ComposeOption<
  | LineSeriesOption
  | GridComponentOption
  | TooltipComponentOption
  | DataZoomComponentOption
>;

echarts.use([
  LineChart,
  GridComponent,
  TooltipComponent,
  DataZoomComponent,
  CanvasRenderer
]);

const Component = defineComponent({
  name: "LineChart",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Object as PropType<{
        axis: string[];
        lineData: number[];
      }>,
      default: () => ({
        axis: [],
        lineData: []
      })
    },
    names: {
      type: Array as PropType<string[]>,
      default: () => []
    },
    percentage: {
      type: Boolean,
      default: false
    },
    times: {
      type: Number,
      default: 1
    }
  },
  setup(props) {
    const store = useStore();
    const { title, data, names, percentage, times } = toRefs(props);
    const dialogVisible = ref(false);
    const hasData = computed(() => {
      return data.value.lineData.length;
    });

    let option: ECOption | null = null;

    watchEffect(async () => {
      if (!hasData.value) return;

      option = {
        tooltip: {
          trigger: "axis"
        },
        xAxis: {
          type: "category",
          data: data.value.axis
        },
        yAxis: {
          type: "value",
          axisLabel: {
            formatter: percentage.value ? "{value}%" : "{value}"
          }
        },
        series: [
          {
            type: "line",
            name: names.value[0] || "Trend",
            data: percentage.value
              ? data.value.lineData.map((it: number) =>
                  Math.round(it * times.value)
                )
              : data.value.lineData.map((it: number) => Math.round(it))
          }
        ]
      };

      if (percentage.value) {
        (option!.tooltip as TooltipComponentOption).formatter = function(
          params: any
        ) {
          return `
            <div style="min-width: 240px;">
              ${params[0].name} <br />
                ${params
                  .map(
                    (it: any) => `
                  <div  style="display: flex; justify-content: space-between;">
                    <span>${it.marker} ${it.seriesName}</span> ${it.value}% <br />
                  </div>
                `
                  )
                  .join("")}
            </div>
          `;
        };
      }

      await nextTick();
      const chart = echarts.init(
        document.getElementById("line-chart-main-" + title.value)!
      );
      chart.setOption(option);
      store.commit(AddResizer, chart);
    });

    const onExpandClick = async () => {
      dialogVisible.value = true;

      await nextTick();
      const chart = echarts.init(
        document.getElementById("line-chart-dialog-" + title.value)!
      );
      chart.setOption({
        ...option,
        dataZoom: [
          {
            type: "inside"
          }
        ]
      });
      store.commit(AddResizer, chart);
    };

    return {
      dialogVisible,
      hasData,

      onExpandClick
    };
  }
});

export default Component;
