<template>
  <div id="bar-chart-card-component">
    <div class="top-container">
      <h3 class="title">{{ title }}</h3>
      <div v-if="hasData" class="expand-button" @click="onExpandClick">
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>
    <div v-if="hasData" class="chart-container">
      <div :id="'bar-chart-main-' + title" class="chart-main"></div>
    </div>
    <div v-else class="chart-empty">
      <h3>No Data</h3>
    </div>

    <el-dialog :title="title" v-model="dialogVisible" width="80%">
      <div class="dialog-container">
        <div :id="'bar-chart-dialog-' + title" class="chart-dialog"></div>
        <p class="hint">Scrolling to zoom.</p>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  ref,
  toRefs,
  watchEffect,
  nextTick,
  computed,
  PropType
} from "vue";
import * as echarts from "echarts/core";
import { BarChart, BarSeriesOption } from "echarts/charts";
import {
  DataZoomComponent,
  DataZoomComponentOption,
  GridComponent,
  GridComponentOption,
  TooltipComponent,
  TooltipComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";
import { useStore } from "vuex";
import { AddResizer } from "@/typings/store";

type ECOption = echarts.ComposeOption<
  | BarSeriesOption
  | GridComponentOption
  | TooltipComponentOption
  | DataZoomComponentOption
>;

echarts.use([
  BarChart,
  GridComponent,
  TooltipComponent,
  DataZoomComponent,
  CanvasRenderer
]);

const Component = defineComponent({
  name: "BarChart",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Object as PropType<{ axis: string[]; series: any[] }>,
      default: () => ({ axis: [], series: [] })
    },
    percentage: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const { title, data, percentage } = toRefs(props);
    const store = useStore();
    const dialogVisible = ref(false);
    const hasData = computed(() => {
      return data.value.series.length;
    });

    let option: ECOption | null = null;

    watchEffect(async () => {
      if (!data.value.series.length) return;

      option = {
        tooltip: {
          trigger: "axis",
          formatter(params: any) {
            return `
              ${params[0].name} <br />
                ${params
                  .map(
                    (it: any) => `
                  <div  style="display: flex; justify-content: space-between;">
                    <span>${it.marker} ${it.seriesName}</span> ${it.value}% <br />
                  </div>
                `
                  )
                  .join("")}
            `;
          }
        },
        xAxis: {
          type: "category",
          data: data.value.axis
        },
        yAxis: {
          type: "value",
          axisLabel: {
            formatter: percentage.value ? "{value}%" : "{value}"
          }
        },
        series: data.value.series
      };

      await nextTick();
      const chart = echarts.init(
        document.getElementById("bar-chart-main-" + title.value)!
      );
      chart.setOption(option);
      store.commit(AddResizer, chart);
    });

    const onExpandClick = async () => {
      dialogVisible.value = true;
      await nextTick();

      const chart = echarts.init(
        document.getElementById("bar-chart-dialog-" + title.value)!
      );
      chart.setOption({
        ...option,
        dataZoom: [
          {
            type: "inside"
          }
        ]
      });
      store.commit(AddResizer, chart);
    };

    return {
      dialogVisible,
      hasData,

      onExpandClick
    };
  }
});

export default Component;
</script>

