
import { defineComponent, ref, toRefs, watchEffect } from "vue";

import RadarChartCard from "@/components/RadarChartCard.vue";
import LineChartCard from "@/components/LineChartCard.vue";
import {
  getCashToCLRatioQChart,
  getCashToCLRatioYChart,
  getDaysPayableOutstandingQChart,
  getDaysPayableOutstandingYChart,
  getFinanceRadarChart,
  getFinanceRegionChart
} from "@/apis/brandInsight";
import {
  FinanceRegionChartResponse,
  LineChartResponse,
  RadarChartResponse
} from "@/typings/brandInsight";
import { formatFinanceRegionData, formatLineData } from "@/utils/brandInsight";

const Component = defineComponent({
  name: "FinancialPower",
  props: {
    brand: {
      type: String,
      default: ""
    }
  },
  components: {
    RadarChartCard,
    LineChartCard
  },
  setup(props) {
    const { brand } = toRefs(props);
    const financeRegionChart = ref<FinanceRegionChartResponse | null>(null);
    const financeRadarChart = ref<RadarChartResponse | null>(null);
    const daysPayableOutstandingQChart = ref<LineChartResponse | null>(null);
    const daysPayableOutstandingYChart = ref<LineChartResponse | null>(null);
    const cashToCLRatioQChart = ref<LineChartResponse | null>(null);
    const cashToCLRatioYChart = ref<LineChartResponse | null>(null);

    watchEffect(async () => {
      if (!brand.value) return;

      financeRegionChart.value = (
        await getFinanceRegionChart(brand.value)
      ).data;
      financeRadarChart.value = (await getFinanceRadarChart(brand.value)).data;
      daysPayableOutstandingQChart.value = (
        await getDaysPayableOutstandingQChart(brand.value)
      ).data;
      daysPayableOutstandingYChart.value = (
        await getDaysPayableOutstandingYChart(brand.value)
      ).data;
      cashToCLRatioQChart.value = (
        await getCashToCLRatioQChart(brand.value)
      ).data;
      cashToCLRatioYChart.value = (
        await getCashToCLRatioYChart(brand.value)
      ).data;
    });

    return {
      financeRegionChart,
      financeRadarChart,
      daysPayableOutstandingQChart,
      daysPayableOutstandingYChart,
      cashToCLRatioQChart,
      cashToCLRatioYChart,

      formatFinanceRegionData,
      formatLineData
    };
  }
});

export default Component;
