<template>
  <div ref="refComponent" id="boxplot-chart-card-component">
    <div class="top-container">
      <h3 class="title">
        {{ title }}
        <p class="subtitle">last 30 Days</p>
      </h3>
      <div v-if="hasData" class="expand-button" @click="onExpandClick">
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>
    <div v-if="hasData" class="chart-container">
      <div :id="'boxplot-chart-main-' + title" class="chart-main"></div>
    </div>
    <div v-else class="chart-empty">
      <h3>No Data</h3>
    </div>

    <el-dialog :title="title" v-model="dialogVisible" width="80%">
      <div class="dialog-container">
        <div :id="'boxplot-chart-dialog-' + title" class="chart-dialog"></div>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  ref,
  toRefs,
  watchEffect,
  nextTick,
  computed,
  PropType
} from "vue";
import * as echarts from "echarts/core";
import { BoxplotChart, BoxplotSeriesOption } from "echarts/charts";
import {
  GridComponent,
  GridComponentOption,
  TooltipComponent,
  TooltipComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";
import { BoxPlotResponse } from "@/typings/brandInsight";
import { AddResizer } from "@/typings/store";
import { useStore } from "vuex";

type ECOption = echarts.ComposeOption<
  BoxplotSeriesOption | GridComponentOption | TooltipComponentOption
>;

echarts.use([BoxplotChart, GridComponent, TooltipComponent, CanvasRenderer]);

const Component = defineComponent({
  name: "BoxplotChartCard",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Object as PropType<BoxPlotResponse>,
      default: () => ({
        categories: [],
        data: []
      })
    }
  },
  setup(props) {
    const { title, data } = toRefs(props);
    const store = useStore();
    const refComponent = ref(null);
    const dialogVisible = ref(false);
    const hasData = computed(() => {
      return data.value.data.length;
    });

    let option: ECOption | null = null;

    watchEffect(async () => {
      if (!hasData.value) return;

      option = {
        tooltip: {
          trigger: "axis"
        },
        grid: {
          left: "20%"
        },
        xAxis: {
          name: "Price",
          nameLocation: "middle",
          nameGap: 30,
          scale: true
        },
        yAxis: {
          type: "category",
          data: data.value.categories,
          axisLabel: {
            formatter(value: any) {
              const SPLIT_MAX = 10;
              if (value.length > SPLIT_MAX) {
                return value.slice(0, SPLIT_MAX - 1) + "...";
              }
              return value;
            }
          }
        },
        series: [
          {
            name: "boxplot",
            type: "boxplot",
            data: data.value.data
          }
        ]
      };

      await nextTick();
      const chart = echarts.init(
        document.getElementById("boxplot-chart-main-" + title.value)!
      );
      chart.setOption(option);
      store.commit(AddResizer, chart);
    });

    const onExpandClick = async () => {
      if (!option) return;
      dialogVisible.value = true;

      await nextTick();
      const chart = echarts.init(
        document.getElementById("boxplot-chart-dialog-" + title.value)!
      );
      chart.setOption({
        ...option,
        yAxis: {
          ...option.yAxis,
          // @ts-ignore
          axisLabel: { ...option.yAxis.axisLabel, interval: 0 }
        }
      });
      store.commit(AddResizer, chart);
    };

    return {
      refComponent,
      dialogVisible,
      hasData,

      onExpandClick
    };
  }
});

export default Component;
</script>

