
import { defineComponent, watchEffect, ref, toRefs } from "vue";

import BoxplotChartCard from "@/components/BoxplotChartCard.vue";
import BarChartCard from "@/components/BarChartCard.vue";
import LineChartCard from "@/components/LineChartCard.vue";
import TableCard from "@/components/TableCard.vue";
import PieChartCard from "@/components/PieChartCard.vue";

import { Column } from "@/constants/element";
import {
  getCategoriesCountTable,
  getMaterialPieChart,
  getProductDiscountTrendChart,
  getProductTypePriceBoxPlotChart,
  getSportPieChart,
  getSportsCountTable,
  getTopMaterialsUsedChart,
  getTopRecycleMaterialsUsedChart
} from "@/apis/brandInsight";
import {
  BoxPlotChartResponse,
  LineBarChartResponse,
  PieChartResponse,
  StackBarChartResponse,
  TableResponse
} from "@/typings/brandInsight";
import { loading } from "@/utils";
import {
  formatLineData,
  formatPieChartData,
  formatStackBarData,
  formatTableData
} from "@/utils/brandInsight";

const columns: Column[] = [
  { prop: "category", label: "Category" },
  { prop: "products", label: "Product Count", width: "80px" },
  { prop: "percentage", label: "%", width: "80px" }
];

const Component = defineComponent({
  name: "ProductPower",
  props: {
    brand: {
      type: String,
      default: ""
    }
  },
  components: {
    BoxplotChartCard,
    BarChartCard,
    LineChartCard,
    TableCard,
    PieChartCard
  },
  setup(props) {
    const { brand } = toRefs(props);
    const productTypePriceBoxPlot = ref<BoxPlotChartResponse | null>(null);
    const productDiscountTrend = ref<LineBarChartResponse | null>(null);
    const topMaterialsUsed = ref<StackBarChartResponse | null>(null);
    const topRecycleMaterialsUsed = ref<StackBarChartResponse | null>(null);
    const sportsCountTable = ref<TableResponse | null>(null);
    const categoriesCountTable = ref<TableResponse | null>(null);
    const sportPie = ref<PieChartResponse | null>(null);
    const materialPie = ref<PieChartResponse | null>(null);

    watchEffect(async () => {
      if (!brand.value) return;

      productTypePriceBoxPlot.value = (
        await getProductTypePriceBoxPlotChart(brand.value)
      ).data;
      productDiscountTrend.value = (
        await getProductDiscountTrendChart(brand.value)
      ).data;
      topMaterialsUsed.value = (
        await getTopMaterialsUsedChart(brand.value)
      ).data;
      topRecycleMaterialsUsed.value = (
        await getTopRecycleMaterialsUsedChart(brand.value)
      ).data;
      sportsCountTable.value = (await getSportsCountTable(brand.value)).data;
      categoriesCountTable.value = (
        await getCategoriesCountTable(brand.value)
      ).data;
      materialPie.value = (await getMaterialPieChart(brand.value)).data;
      sportPie.value = (await getSportPieChart(brand.value)).data;
    });

    return {
      columns,

      productTypePriceBoxPlot,
      productDiscountTrend,
      topMaterialsUsed,
      topRecycleMaterialsUsed,
      sportsCountTable,
      categoriesCountTable,
      sportPie,
      materialPie,

      formatLineData,
      formatStackBarData,
      formatTableData,
      formatPieChartData
    };
  }
});

export default Component;
