
import {
  defineComponent,
  toRefs,
  PropType,
  watchEffect,
  computed,
  nextTick
} from "vue";
import * as echarts from "echarts/core";
import * as _ from "ramda";
import { PieChart, PieSeriesOption } from "echarts/charts";
import {
  GridComponent,
  GridComponentOption,
  LegendComponentOption,
  TooltipComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";

type ECOption = echarts.ComposeOption<
  | PieSeriesOption
  | GridComponentOption
  | LegendComponentOption
  | TooltipComponentOption
>;

echarts.use([PieChart, GridComponent, CanvasRenderer]);

const Component = defineComponent({
  name: "OverallCard",
  props: {
    title: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: "pie"
    },
    value: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 100
    },
    messages: {
      type: Array as PropType<string[]>,
      default: () => []
    }
  },
  setup(props) {
    const { value, title, type, max } = toRefs(props);
    const hasData = computed(() => {
      return value.value;
    });

    let option: ECOption | null = null;

    watchEffect(async () => {
      if (!hasData.value) return;

      option = {
        tooltip: {
          trigger: "item",
          formatter(param: any) {
            if (param.dataIndex !== 0) return "";
            return `
              <div style="display: flex; justify-content: space-between;">
                <span style="margin-right: 15px;">${
                  param.marker
                } Ratio</span> ${
              max.value === 100 ? param.value + "%" : param.value
            } <br />
              </div>
            `;
          }
        },
        series: [
          {
            type: "pie",
            radius: ["70%", "90%"],
            data: [value.value, max.value - value.value],
            itemStyle: {
              // @ts-ignore
              color(params: any) {
                if (params.dataIndex === 1) {
                  return "#eeeeee";
                }
                return params.color;
              }
            },
            label: {
              show: false,
              position: "center"
            }
          }
        ]
      };

      await nextTick();
      if (type.value === "pie" && option) {
        const chart = echarts.init(
          document.getElementById("overall-pie-chart-" + title.value)!
        );
        chart.setOption(option);
      }
    });

    return {
      hasData,

      _
    };
  }
});

export default Component;
