<template>
  <div id="overall-card-component">
    <div v-if="hasData" class="left-container">
      <div
        v-if="type === 'pie'"
        :id="'overall-pie-chart-' + title"
        class="pie-chart"
      ></div>
      <div v-if="type === 'growth'" class="growth-chart">
        <font-awesome-icon
          class="icon"
          :class="{ up: value >= 0, down: value < 0 }"
          :icon="['fas', value >= 0 ? 'level-up-alt' : 'level-down-alt']"
        ></font-awesome-icon>
      </div>
    </div>
    <div v-else>
      <h3>Empty</h3>
    </div>

    <div class="right-container">
      <h3 class="title">{{ title }}</h3>
      <div v-if="hasData" class="text-wrapper">
        <p v-if="type === 'pie'">{{ max === 100 ? value + "%" : value }}</p>
        <p v-if="type === 'growth'">
          {{ value >= 0 ? "+" : "-" }} {{ _.splitAt(5, `${value * 100}`)[0] }}%
        </p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  toRefs,
  PropType,
  watchEffect,
  computed,
  nextTick
} from "vue";
import * as echarts from "echarts/core";
import * as _ from "ramda";
import { PieChart, PieSeriesOption } from "echarts/charts";
import {
  GridComponent,
  GridComponentOption,
  LegendComponentOption,
  TooltipComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";

type ECOption = echarts.ComposeOption<
  | PieSeriesOption
  | GridComponentOption
  | LegendComponentOption
  | TooltipComponentOption
>;

echarts.use([PieChart, GridComponent, CanvasRenderer]);

const Component = defineComponent({
  name: "OverallCard",
  props: {
    title: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: "pie"
    },
    value: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 100
    },
    messages: {
      type: Array as PropType<string[]>,
      default: () => []
    }
  },
  setup(props) {
    const { value, title, type, max } = toRefs(props);
    const hasData = computed(() => {
      return value.value;
    });

    let option: ECOption | null = null;

    watchEffect(async () => {
      if (!hasData.value) return;

      option = {
        tooltip: {
          trigger: "item",
          formatter(param: any) {
            if (param.dataIndex !== 0) return "";
            return `
              <div style="display: flex; justify-content: space-between;">
                <span style="margin-right: 15px;">${
                  param.marker
                } Ratio</span> ${
              max.value === 100 ? param.value + "%" : param.value
            } <br />
              </div>
            `;
          }
        },
        series: [
          {
            type: "pie",
            radius: ["70%", "90%"],
            data: [value.value, max.value - value.value],
            itemStyle: {
              // @ts-ignore
              color(params: any) {
                if (params.dataIndex === 1) {
                  return "#eeeeee";
                }
                return params.color;
              }
            },
            label: {
              show: false,
              position: "center"
            }
          }
        ]
      };

      await nextTick();
      if (type.value === "pie" && option) {
        const chart = echarts.init(
          document.getElementById("overall-pie-chart-" + title.value)!
        );
        chart.setOption(option);
      }
    });

    return {
      hasData,

      _
    };
  }
});

export default Component;
</script>

