import { AxiosPromise } from "axios";
import request from "@/apis/http";
import {
  BoxPlotChartResponse, FinanceRegionChartResponse, GrowthChartResponse,
  LineBarChartResponse, LineChartResponse, OverallChartResponse,
  PieChartResponse, RadarChartResponse,
  StackBarChartResponse,
  TableResponse
} from "@/typings/brandInsight";

export function getBrandOptions(): AxiosPromise<{brands: string[]}> {
  return request({
    url: '/brand_insight/brands/',
    method: 'GET'
  })
}

// *** For Product Power Page
export function getProductTypePriceBoxPlotChart(brand: string): AxiosPromise<BoxPlotChartResponse> {
  return request({
    url: `/brand_insight/chart/ProductTypePriceBoxPlot/${brand}/`,
    method: "GET",
  });
}

